/**
 * transition  mixin
 *
 * @prop speed - 
 *
 * @event beforeEnter - 
 * @event enter - 
 * @event afterEnter - 
 * @event beforeLeave - 
 * @event leave - 
 * @event afterLeave - 
 */

export default {
  props: {
    speed: {
      type: [Number, String],
      default: 'normal'
    }
  },

  computed: {
    transitionTime() {
      switch (this.speed) {
        case 'normal':
          return '300ms'
        case 'fast':
          return '150ms'
        case 'slow':
          return '450ms'
        default:
          return '300ms'
      }
    }
  }
}


