/**
 * input 
 *
 * @prop hidden - 
 * @prop initVal - 
 * @prop number -  nmuber 
 * @prop placeholder - 
 * @prop queryName - 
 * @prop readOnly - 
 * @prop required - 
 * @prop row - textarea 
 * @prop textLengthTip - 
 * @prop type - ( text | textarea )
 * @prop theme - 
 *
 * @prop completion - 
 * @prop errorMessage - input 
 * @prop errorTipType -  bubble | tip 
 * @prop formatMessage - 
 * @prop min - inputtextarea 
 * @prop max - inputtextarea 
 * @prop regex - 
 * @prop verifedType - 
 *
 * @event change - input
 * @event blur - inputblur
 * @event focus - inputfocus
 * @event keyup - inputkeyup
 *
 */

import './input.scss'
import render from './input.render'

import store from '../../../vuex/store'
import hubStore from '../../../vuex/module/hub/type.json'
import initVerfication from './validate.js'

import baseMixin from '../../../mixin/base'
import formMixin from '../../../mixin/form'

import rowComp from '../../common/layout/row/row'
import colComp from '../../common/layout/col/col'

import { dataType } from '../../../util/data/data'

const tip = {}

const KEYUP_INTERVAL_TIME = 500
const TYPE_TEXT_AREA = 'textarea'
const TYPE_TEXT = 'text'
const ERROR_MESSAGE_TIP = 'tip'
const ERROR_MESSAGE_BUBBLE = 'bubble'

const inputComp = {
  name: 'input',

  render,

  mixins: [baseMixin, formMixin],

  components: {
    row: rowComp,
    column: colComp
  },

  store,

  props: {
    hidden: {
      type: Boolean,
      default: false
    },

    initVal: {
      type: [String, Number],
      default: ''
    },

    number: {
      type: Boolean,
      default: false
    },

    placeholder: {
      type: String,
      default: ''
    },

    queryName: {
      type: String,
      default: ''
    },

    errorTipName: String,

    readOnly: {
      type: Boolean,
      default: false
    },

    row: {
      type: Number,
      default: 4
    },

    textLengthTip: {
      type: Boolean,
      default: false
    },

    type: {
      type: String,
      default: 'text'
    },

    required: {
      type: Boolean,
      default: true
    },

    errorMessage: {
      type: String,
      default: ''
    },

    errorTipType: {
      type: String,
      default: 'tip'
    },

    formatMessage: String,

    min: Number,

    max: Number,

    regex: String,

    verifedType: String,

    completion: {
      type: Boolean,
      default: false
    }
  },

  data() {
    // 
    this.compName = 'input'

    return {
      // 
      value: this.number ? this._switchNum(this.initVal) : this.initVal,
      //  focus 
      focusing: false,
      //  keyup 
      keyuping: false,
      // 
      dangerTip: '',
      // 
      dataTypeName: '',
      // 
      verified: true,
      // 
      bubbleDisplay: false,
      // 
      inputTextLength: 0,
    }
  },

  computed: {
    // 
    cPrefix() {
      return `${this.compPrefix}-input`
    },
    // 
    _formatMessage() {
      return this.errorMessage ? this.errorMessage + '' : this.dataTypeName + ''
    },
    dangerTipDisplay() {
      return !!this.dangerTip && this.bubbleDisplay
    },
    isTextarea() {
      return this.type === TYPE_TEXT_AREA
    },
    isText() {
      return this.type === TYPE_TEXT
    },
    errorBorderDisplay() {
      return !this.verified
    },
    inputHub() {
      return this.$store.getters[hubStore.input.get]
    },
    //  stage  class 
    stageClass() {
      return [{
        [`${this.cPrefix}-textarea-stage`]: this.isTextarea
      }]
    },
    wrapClass() {
      return [
        this.xclass('wrap'),
        {
          [`${this.cPrefix}-editting`]: this.focusing
        },
        {
          [`${this.cPrefix}-error-border`]: this.errorBorderDisplay
        }
      ]
    },
    // input 
    inputBoxCol() {
      let slotHead = this.$slots.head
      let slotTail = this.$slots.tail

      if (slotHead && slotTail) {
        return 10
      } else if (slotHead || slotTail) {
        return 11
      } else {
        return 12
      }
    }
  },

  methods: {
    /**
     * 
     * @return {Object} this - 
     */
    _initVerfication() {
      if (this.regex) {
        this.regexObj = new RegExp(this.regex)

        return this
      }

      var verify = initVerfication(this.verifedType)

      if (verify) {
        this.regexObj = verify.regex
        this.dataTypeName = verify.dataTypeName
      }

      return this
    },

    /**
     * 
     * @return {Object} this - 
     */
    _initVerfiedMessage() {
      let errorMessage = this.errorMessage

      if (errorMessage) {
        this.emptyMessage = errorMessage
        this.lengthMessage = errorMessage

        return this
      }

      this.emptyMessage = this.errorMessage ? this.errorMessage : ''
      this.lengthMessage = this.errorMessage ? this.errorMessage : ''

      return this
    },

    /**
     *  value  change 
     * @return {Object} this - 
     */
    _dispatchChange() {
      return this.$emit('change', {
        emitter: this,
        value: this.value
      })
    },

    /**
     * 
     *
     * @return {Object} -
     *                  verified - 
     *                  dangerTip - 
     */
    _verifyEmpty(firstVerify) {
      let dangerTip = ''

      if (this.required) {
        if (this.bubbleDisplay) {
          dangerTip = firstVerify ? '' : `${this.emptyMessage}!`
        } else {
          dangerTip = `${this.emptyMessage}!`
        }

        return {
          verified: false,
          dangerTip
        }
      }

      return {
        verified: true,
        dangerTip
      }
    },

    /**
     * 
     *
     * @param {Boolean} - 
     * @return {Object} - this - 
     */
    verify(firstVerify) {
      let verified = true
      let dangerTip = ''

      const returnFun = () => {
        if (!verified) {
          document.body.scrollTop = this.$el.offsetTop
        }

        this.verified = verified
        this.dangerTip = dangerTip

        return verified
      }

      if (!this.number) {
        this.value = this.value.trim()
      }

      if (!this.value && this.value !== 0) {
        let verifyEmpty = this._verifyEmpty()

        verified = verifyEmpty.verified
        dangerTip = verifyEmpty.dangerTip

        return returnFun()
      } else {
        if (this.number && isNaN(this.value)) {
          dangerTip = `${this.errorMessage}`
          verified = false

          return returnFun()
        }

        if (this.min) {
          if (this.number) {
            verified = this.min <= this.value
            dangerTip = verified ? '' : `${this.lengthMessage}${this.min}!`
          } else {
            verified = this.min <= this.value.toString().length
            dangerTip = verified ? '' : `${this.lengthMessage}${this.min}!`
          }

          if (!verified) {
            return returnFun()
          }
        }

        if (this.max) {
          if (this.number) {
            verified = this.max >= this.value
            dangerTip = verified ? '' : `${this.lengthMessage}${this.max}!`
          } else {
            verified = this.max >= this.value.toString().length
            dangerTip = verified ? '' : `${this.lengthMessage}${this.max}!`
          }

          if (!verified) {
            return returnFun()
          }
        }

        if ((this.regex || this.verifedType) && !this.regexObj.test(this.value)) {
          verified = false

          if (firstVerify) {
            dangerTip = ''
          } else {
            dangerTip = this.formatMessage ? this.formatMessage : this._formatMessage
          }

          return returnFun()
        }
      }

      return returnFun()
    },

    /**
     * 
     *
     * @return {Object} - this - 
     */
    validate() {
      this.verify()

      if (!this.verified) {
        tip(this.dangerTip)

        return false
      }

      return this
    },

    /**
     *  focus 
     * @return {Object} this - 
     */
    focus(evt) {
      this.verified = true
      this.focusing = true

      return this.$emit('focus', {
        emitter: this,
        valeu: this.value,
        event: evt
      })
    },

    /**
     *  blur 
     * @return {Object} this - 
     */
    blur(evt) {
      this.focusing = false

      if (this.number) {
        this.value = this._switchNum(this.value)
      }

      return this.$emit('blur', {
        emitter: this,
        valeu: this.value,
        event: evt
      })
    },

    /**
     *  keyup 
     * @return {Object}
     */
    keyup() {
      if (this.keyuping) {
        return false
      }

      this.keyuping = true

      setTimeout(() => {
        this.keyuping = false
      }, KEYUP_INTERVAL_TIME)
    },

    /**
     *  -  16 
     */
    _switchNum(val) {
      if (val === 0 || val === '0') {
        return 0
      }

      let strTemp = String(val)

      if (isNaN(strTemp)) {
        let temp = strTemp

        strTemp = strTemp.replace(/[^\d.]+/g, '')

        if (temp.indexOf('-') === 0) {
          strTemp = '-' + strTemp
        }
      }

      if (isNaN(strTemp)) {
        strTemp = ''
      }

      if (val.length >= 16) {
        return strTemp
      }

      return Number(strTemp)
    }
  },

  watch: {
    initVal(val, oldVal) {
      this.value = val
    },
    value(val, oldVal) {
      // 
      this.limitLen = String(val).length

      if (this.completion && this.$slots.completion) {
        this.$slots.completion[0].componentInstance.search(val)
      }

      this._dispatchChange()
    }
  },

  created() {
    this.bubbleDisplay = this.errorTipType !== ERROR_MESSAGE_TIP
  },

  mounted() {
    this._initVerfication()
    this._initVerfiedMessage()

    this.$store.dispatch(hubStore.input.add, this)
  }
}

export default inputComp



