/**
 * btn 
 *
 * @prop ban - 
 * @prop kind - 
 * @prop link - 
 * @prop radius - none | S | M | L
 * @prop size - 
 * @prop submit - 
 * @prop type -  (button | flat | float | outline)
 * @prop textDisplay - 
 * @prop value - 
 *
 * @event click - btn
 */

import './btn.scss'

import loadingComp from '../../base/loading/loading'
import render from './btn.render'
import baseMixin from '../../../mixin/base'
import formMixin from '../../../mixin/form'

const BTN_TYPE_LINK = 'link'
const BTN_TYPE_BUTTON = 'button'

const SIZE_S = 'S'
const SIZE_M = 'M'
const SIZE_L = 'L'

const btnComp = {
  name: 'btn',

  mixins: [baseMixin, formMixin],

  render,

  components: {
    loading: loadingComp
  },

  props: {
    ban: {
      type: Boolean,
      default: false
    },

    kind: {
      type: String,
      default: 'primary'
    },

    link: String,

    outline: {
      type: Boolean,
      default: false
    },

    radius: {
      type: String,
      default: 's'
    },

    type: {
      type: String,
      default: BTN_TYPE_BUTTON
    },

    value: {
      type: String,
      require: true
    },

    size: {
      type: String,
      default: SIZE_S
    },

    submit: {
      type: Boolean,
      require: false
    },

    textDisplay: {
      type: Boolean,
      default: false
    }
  },

  data() {
    return {
      // 
      banState: false,
      // 
      btnValueDisplay: false,
      //  loading 
      createdLoading: false,
      // 
      motion: false
    }
  },

  watch: {
    ban(val) {
      this.banState = val
    }
  },

  methods: {
    _setDataOpt() {
      this.banState = this.ban
    },

    mouseup() {

    },

    mousedown() {

    },

    focus() {
      this.motion = true
    },

    blur() {
      this.motion = false
    },

    /**
     * 
     * @return {Object} this - 
     */
    click() {
      return this.$emit('click')
    },

    /**
     * 
     */
    banBtn() {
      this.banState = true
    },

    /**
     * 
     */
    allowBtn() {
      this.banState = false
    },

    /**
     * 
     */
    openLoading() {
      if (!this.createdLoading) {
        this.createdLoading = true
        this.banBtn()
      }

      this.$nextTick(() => {
        this.$refs.loading.show()
      })
    },

    /**
     * 
     */
    classLoading(state) {
      this.allowBtn()
      this.$refs.loading.hide()
    }
  },

  computed: {
    // 
    cPrefix() {
      return `${this.compPrefix}-btn`
    },

    isLink() {
      return !this.btnValueDisplay && this.link
    },

    isButton() {
      return !this.btnValueDisplay && this.type === BTN_TYPE_BUTTON
    },

    btnClass() {
      return this.xclass([
        this.themeClass,
        `kind-${this.kind}`,
        `size-${this.size.toLowerCase()}`,
        `radius-${this.radius.toLowerCase()}`,
        `type-${this.type}`
      ])
    }
  }
}

export default btnComp



