/**
 * modal 
 *
 * @prop commit -  full 
 *                
 * @prop header - 
 * @prop message - 
 * @prop okBtn - 
 * @prop noBtn - 
 * @prop okBtnDisplay - 
 * @prop noBtnDisplay - 
 * @prop headerDisplay - 
 * @prop footerDisplay - 
 * @prop type - full | alert | confirm | simple | long
 *
 * @slot - 
 *
 * @event ok - 
 * @event no - 
 */

import './modal.scss'
import './modal.m.scss'

import render from './modal.render'
import baseMixin from '../../../mixin/base'

import popComp from '../../base/pop/pop'
import btnComp from '../../base/btn/btn'
import iconComp from '../../base/icon/icon'
import scrollerComp from '../../base/scroller/scroller'
import rowComp from '../../common/layout/row/row'
import colComp from '../../common/layout/col/col'

import fadeTransition from '../../transition/fade'
import noTransition from '../../transition/no'

const TYPE_ALERT = 'alert'
const TYPE_CONFIRM = 'confirm'
const TYPE_TIP = 'tip'

const TIP_SHOW_TIME = 1500

const modalComp = {
  name: 'modal',

  render,

  mixins: [baseMixin],

  components: {
    btn: btnComp,
    icon: iconComp,
    pop: popComp,
    scroller: scrollerComp,
    row: rowComp,
    column: colComp,
    'fade-transition': fadeTransition,
    'no-transition': noTransition
  },

  computed: {
    // 
    cPrefix() {
      return `${this.compPrefix}-modal`
    },
    //  header  class 
    headerClass() {
      return {
        [`${this.cPrefix}-no-header`]: !this.headerDisplay,
        [`${this.cPrefix}-no-header-title`]: !this.modalHeader
      }
    },
    //  footer  class 
    footerClass() {
      return { [`${this.cPrefix}-no-footer`]: !this.footerDisplay }
    },
    //  full modal
    isFull() {
      return this.type === 'full'
    },
    //  full 
    isBiggerFull() {
      return this.isFull && this.deviceRange > 575
    }
  },

  props: {
    commit: {
      type: Boolean,
      default: false
    },
    type: {
      type: String,
      default: TYPE_CONFIRM
    },
    header: {
      type: String,
      default: ''
    },
    okBtn: {
      type: String,
      default: ''
    },
    noBtn: {
      type: String,
      default: ''
    },
    message: {
      type: String,
      default: ''
    },
    headerDisplay: {
      type: Boolean,
      default: true
    },
    headerNoBtnDisplay: {
      type: Boolean,
      default: true
    },
    noBtnDisplay: {
      type: Boolean,
      default: true
    },
    footerDisplay: {
      type: Boolean,
      default: true
    }
  },

  data: () => {
    return {
      pointStart: {
        x: 0,
        y: 0
      },
      isMousedown: false,
      modalDisplay: false,
      modalMessage: '',
      modalHeader: '',
      // scroller 
      hasScroller: false
    }
  },

  methods: {
    _init() {
      this._initModal()
    },

    _initModal() {
      this.$el.style.visibility = 'hidden'
      this.$el.style.display = ''

      this.$refs.pop.$el.style.visibility = 'hidden'
      this.$refs.pop.$el.style.display = ''

      this.$refs.scroller.$on('changeScroller', ({ scrollerHeight }) => {
        this.$refs.pop.computePosition()

        this.$refs.pop.$el.style.visibility = ''
        this.$refs.pop.$el.style.display = 'none'

        this.$el.style.visibility = ''
        this.$el.style.display = 'none'

        this.$refs.scroller.$off()
      })

      this.$refs.scroller.$on('changeYBar', ({ hasScroller }) => {
        this.hasScroller = hasScroller
      })
    },

    /**
     * 
     */
    _setDataOpt() {
      this.modalMessage = this.message
      this.modalHeader = this.header
    },

    /**
     * pop
     *
     * @param {Number} - 
     * @return {Object}
     */
    show() {
      this.modalDisplay = true
      this.$refs.pop.show()

      return this
    },

    /**
     * pop
     *
     * @return {Object}
     */
    hide() {
      this.$refs.pop.hide({
        cb: () => {
          this.modalDisplay = false
          this.isMousedown = false
        }
      })

      return this
    },

    /**
     * mouseDown 
     *
     * @return {Object}
     */
    mouseDown(event) {
      this.isMousedown = true

      this.pointStart = {
        x: event.clientX,
        y: event.clientY
      }

      return this
    },

    /**
     * mouseMove 
     *
     * @return {Object, Boolean}
     */
    mouseMove(event) {
      if (!this.isMousedown) {
        return false
      }

      let $this = this.$el.querySelector('.' + this.xclass('pop'))
      let styleHub = getComputedStyle($this)
      let top = parseFloat(styleHub.top, 10)
      let left = parseFloat(styleHub.left, 10)

      $this.style.top = `${top + event.clientY - this.pointStart.y}px`
      $this.style.left = `${left + event.clientX - this.pointStart.x}px`

      this.pointStart = {
        x: event.clientX,
        y: event.clientY
      }

      return this
    },

    /**
     * mouseUp 
     *
     * @return {Object, Boolean}
     */
    mouseUp(event) {
      event.preventDefault()

      if (!this.isMousedown) {
        return false
      }

      this.isMousedown = false

      return this
    },

    /**
     * 
     *
     * @return {Object}
     */
    ok() {
      if (this.okCb) {
        this.okCb(this)

        return this.$emit('ok')
      }

      this.hide()
    },

    /**
     * 
     *
     * @return {Object}
     */
    no() {
      if (this.noCb) {
        this.noCb(this)

        return this.$emit('no')
      }

      this.hide()
    },

    /**
     *  /  title
     *
     * @return {Object, Boolean}
     */
    title(text) {
      if (text === '' || text) {
        this.modalHeader = text
      }

      return this
    },

    /**
     * alert, confirm 
     *
     * @param {String} - 
     * @return {Object, String}
     */
    info(text) {
      if (text === '' || text) {
        this.modalMessage = text
      }

      return this
    },

    /**
     * alert, confirm 
     *  tip 
     *
     * @param {Function}
     * @return {Object}
     */
    setOkCb(cb) {
      this.okCb = cb

      return this
    },

    /**
     * alert, confirm 
     *
     * @param {Function}
     * @return {Object}
     */
    setNoCb(cb) {
      this.noCb = cb

      return this
    }
  }
}

export default modalComp



